@import '../../../../../scss/base/palette',
        '../../../../../scss/base/typography',
        '../../../../../scss/helpers/variables';


nav {
  font-size: $xsm-text;
  letter-spacing: .05em;
  font-weight: $font-weight-normal;
  padding: 1rem 2rem 0 2rem;

  .label {
    display: block;
    font-weight: $font-weight-normal;
    text-transform: uppercase;
    font-size: .65rem;
    margin-top: 2rem;
  }

  a {
    color: $light-grey;
    text-decoration: none;
  }

  .nav-item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-size: .8rem;
    line-height: 1;
    color: $white;

    a {
      flex: 1;
      margin-left: 1em;
      padding: .9em 0;
      transition: color 180ms ease;
      border: none;
      opacity: .7;

      &:hover {
        opacity: 1;
      }

    }

    &:hover {
      opacity: 1;
    }

    &.active {
      opacity: 1;

      a { color: $primary; }

    }

    &[data-count]:after {
      content: attr(data-count);
      flex: 0;
      align-self: center;
      display: flex;
      justify-content: flex-end;
      color: darken($medium-grey, 30);
      transition: color 180ms ease;
    }

    &[data-count]:hover:after {
      color: $white;
    }

    svg {
      opacity: .6;
    }

  }

}
