
// Single image
.field--single-image {
  span.label { margin-left: 0; }

  .image-container {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 100%;
    line-height: 0;

    img { max-width: 100%; }

    .delete {
      position: absolute;
      top: 15px;
      right: 15px;
      cursor: pointer;
    }

  }

}
