@import '../../../../../scss/base/palette',
        '../../../../../scss/base/typography',
        '../../../../../scss/components/select/mixins',
        '../../../../../scss/helpers/variables';

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 4rem;
  background: $black;
  z-index: 1000;
  transition: all .2s linear;


  @media screen and (min-width: $breakpoint-small) 
  {
    transform: translateY(-100%);
    -ms-transform: translateY(-100%);
  }
}

.header > .menu-content { display: none; }
.header.expanded { height: 100vh; }
.header.expanded > .header-top { border-bottom: 1px solid $dark-grey; }
.header.expanded > .menu-content { display: flex; transform: translateY(0); }


.header-top {
  background: $black;
  color: $light-grey;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 2rem;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 4rem;
  z-index: 100;

  @include ie-11-min($breakpoint-small) 
  {
    width: $sidebar-width;
  }
}

.left-group,
.right-group {
  display: flex;
  align-items: center;
}


.logo {
  line-height: 0;
  max-width: 70%;
}


.logo img {
  height: 2rem;
  max-width: 100%;

  &:hover {
    cursor: pointer;
  }

}

.external-link {
  margin-left: auto;
  margin-bottom: 2px;
  line-height: 0;
  color: $medium-grey;
}


.nav-toggle {
  display: block;
  margin-left: 2em;
  line-height: 0;
  cursor: pointer;
}

.menu-content {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: $black;
  display: flex;
  flex-direction: column;
  padding-top: 4em;
  padding-bottom: 3rem;
  transition: transform .2s linear;
  transform: translateY(-100%);
  -ms-transform: translateY(-100%);
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
