.column.list {
  border-bottom: 1px solid $light-grey;
  padding: 1em;
  font-size: $no-text;
  text-transform: uppercase;

  span.date {
    margin-right: 1em;
    font-weight: $font-weight-semibold;
    color: $medium-grey;
  }

}

.edit-action {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: $white;
  border-bottom: 1px solid lighten($medium-grey, 12);
  position: fixed;
  width: 100%;
  height: 4rem;
  z-index: 600;
  padding: 2em;
  transition: width .35s linear;

  @media screen and (min-width: $breakpoint-small) {
    height: 5rem;
  }

  .button + .button {
    margin-left: 1em;
  }

  .right-group {
    display: flex;
    align-items: center;
    margin-right: 3em;
    @media screen and (min-width: 1150px) {
      margin: 0;
    }
  }

  .label { margin: 0; }
}

// Todo: Used for 3 different content types. Rename.
.list {
  flex: 1;
  display: flex;
  flex-direction: column;

  .list-headers {
    display: flex;
    flex-wrap: wrap;
    padding: 0 1em 1em 1em;

    p.list-header {
      flex: 1;
      font-size: $xsm-text;
      text-transform: uppercase;
      color: darken($light-grey, 20);

      &:first-child { flex: 0 75%; }

    }

  }

  .post-item, .folder-item {
    position: relative;
    display: flex;
    flex: 0 0 auto;
    flex-wrap: wrap;
    align-items: center;
    border-bottom: 1px solid $light-grey;
    padding: 1em;

    .title {
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      position: relative;
      padding-right: 1em;

      h3 {
        font-size: $md-text;
        font-weight: 500;
        padding-bottom: .2em;
        text-transform: none;
        cursor: pointer;
        min-height: 1.5em;

        svg {
          margin-right: .5em;
        }

      }

      .post-item--info {
        display: flex;
        align-items: flex-start;
      }

      span.last-edited {
        font-size: $xxsm-text;
        text-transform: uppercase;
        color: darken($light-grey, 35);
        letter-spacing: .05em;
      }

      .language-indicator {
        display: flex;
        align-items: center;
        position: relative;
        font-size: $xxsm-text;
        text-transform: uppercase;
        border-radius: $border-radius;
        margin-left: 1rem;
        font-weight: 500;
        opacity: .6;

        svg {
          padding: 1px;
          margin-bottom: 1px;
          margin-right: 4px;
        }

        &:before {
          position: absolute;
          content: '';
          left: -.5rem;
          top: 0;
          width: 1px;
          height: 100%;
          background: $light-medium-grey;
        }

      }

      &.draft {
        padding-left: 1.5em;

        &:before {
          display: block;
          position: absolute;
          content: 'draft';
          top: 1.8em;
          left: -1.8em;
          background-color: $light-grey;
          color: $dark-grey;
          transform: rotate(-90deg);
          font-size: $xxsm-text;
          font-weight: $font-weight-semibold;
          text-transform: uppercase;
          letter-spacing: .1em;
          text-align: center;
          padding: .25em .8em;
        }

        h3, span.last-edited {
          opacity: .5;
        }

      }

      &.error {
        padding-left: 1.5em;

        &:before {
          display: block;
          position: absolute;
          content: 'error';
          top: 1.8em;
          left: -1.8em;
          background-color: $error;
          color: $white;
          transform: rotate(-90deg);
          font-size: $xxsm-text;
          font-weight: $font-weight-semibold;
          text-transform: uppercase;
          letter-spacing: .1em;
          text-align: center;
          padding: .25em .8em;
        }

      }

    }

    .action {
      flex: 0 0 25%;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      opacity: .2;

      .button { width: 90px; }

      .delete {
        opacity: .35;
        margin-left: 1em;
        transition: opacity 320ms ease;

        &:hover {
          opacity: 1;
        }

      }

    }

    .mini-action {
      display: none;
    }

    .mini-action-bar {
      display: none;
      height: auto;
      flex: 1 100%;
      margin: 1.5em 0 .5em 0;
      transition: all 250ms ease;

      .button {
        flex: 1;
        margin-right: 2em;
        &:last-of-type { margin-right: 0; }
      }

    }

    &:hover {
      background-color: $lighter-grey;
      cursor: pointer;
      .action {
        opacity: 1;
      }

      // Adjust draft tag color when hovering list item
      & > .draft:before { background: darken($light-grey, 5); }
      & > .mini-action-bar > .button.secondary {
        border-color: darken($lighter-grey, 10);
        &:hover { border-color: $error; }
      }

    }

  }

  .folder-item {
    background-color: $lighter-grey;

    .title {

    }

    .action {
      opacity: .5;
      flex: 0 0 25%;

      .delete {
        margin-left: 0;
      }

    }

    &:hover { background-color: darken($lighter-grey, 2); }

  }

}

.fixed-footer-button { display: none; }

@media screen and (max-width: $breakpoint-smallest) {

  .list-header.action { display: none; }

  .list {
    padding-bottom: 6em;

    .post-item, .folder-item {

      .action { display: none; }

      .title { flex: 1 80%; }

      .mini-action {
        display: block;
        flex: 1;
        text-align: right;
      }

      .mini-action.expanded + .mini-action-bar {
        display: flex;
      }

    }

  }

  .fixed-footer-button { display: block; }

}
