@import '../../../../../scss/base/palette',
        '../../../../../scss/base/typography',
        '../../../../../scss/components/select/mixins',
        '../../../../../scss/helpers/variables';

.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  width: $sidebar-width;
  background: $black;
  z-index: 200;
  height: 100vh;
  transform: translateX(-100%);
  transition: transform .35s linear;
  overflow: hidden;

  @media screen and (min-width: $breakpoint-small) {
    transform: translateX(0);
  }

}


.sidebar-top {
  background: $black;
  color: $light-grey;
  border-bottom: 1px solid lighten($dark-grey, 3);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 2rem;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  z-index: 100;

  @include ie-11-min($breakpoint-small) 
  {
    width: $sidebar-width;
  }
}

.left-group,
.right-group {
  display: flex;
  align-items: center;
}

.right-group { margin-left: 1.2em; }

.logo {
  line-height: 0;
}


.logo img {
  height: 2rem;
  max-width: 100%;

  &:hover {
    cursor: pointer;
  }

}

.external-link {
  color: $medium-grey;
  line-height: 0;
  margin-bottom: 2px;
}


.menu-content {
  position: absolute;
  left: 0;
  top: 7rem;
  width: 100%;
  height: calc(100% - 10rem);
  background: $black;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  overflow-x: hidden;
  -ms-overflow-style: none;
  -webkit-overflow-scrolling: touch;
  @media screen and (min-width: 840px) {
    &.with-checklist {
      top: 10rem;
      height: calc(100% - 13rem);
    }
  }
}

.menu-content::-webkit-scrollbar {
 display: none;
}
