.react-tagsinput {
  border-radius: 0.3rem;
  border: 1px dashed $light-medium-grey !important;
  background: none;
  padding: .5em .5em .14em .5em !important;

  .react-tagsinput-tag {
    background-color: #f3f3f3;
    border: none;
    border-radius: 0.3rem;
    color: $black;
    font-family: $font-family-text;
    font-size: $no-text;
    font-weight: 500;
    padding: .4em .6em;
  }

  .react-tagsinput-input {
    color: $dark-grey;
    font-family: $font-family-text;
    font-size: $no-text;
    font-weight: 500;
    width: auto;
  }

  input:focus { border: 0; }

}
.react-tagsinput-remove {
  transition: opacity 0.15s;
  opacity: 0.6;
  &:hover {
    opacity: 0.4;
  }
}
.edit-mode .react-tagsinput {
  margin: 0;
}

.react-tagsinput--focused {
  border: 1px solid $primary !important;
}
