.ProseMirror-selectednode {
  display: inline-block;
  line-height: 0;
  &::selection { background: rgba(0,0,0,0); }
}

.ProseMirror-selectednode img {
  outline: 1px solid $primary;
  &::selection { background: rgba(0,0,0,0); }
}

hr.ProseMirror-selectednode { background: $primary !important; }

.ProseMirror > p { margin-bottom: 1rem; }
.ProseMirror > pre { margin: 0 0 1rem 0; }

.ProseMirror-gapcursor {
  border-left: 1px solid black;
  height: 1rem;
}


.ProseMirror > .table-placeholder {
  display: block;
  width: 100%;
  height: auto;
  border: 1px solid $light-medium-grey;
  border-radius: $border-radius;
  padding: 0.5em 1rem;
  margin-bottom: 1rem;
  color: #3A3A3A;
}