html, body, #app {
  margin:0;
  height:100%;
  min-height:100%;
}

.wrapper {
  height: 100%;
  margin: 0;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow-y: auto;
  overflow-x: hidden;

  .main-content {
    flex: 1;
    order: 1;
    margin-top: 4rem;
    margin-left: 0;
    position: relative;
    overflow-x: hidden;
    overflow-y: auto;

    @media screen and (min-width: $breakpoint-small) {
      flex-direction: row;
      margin-top: 0;
      margin-left: $sidebar-width;
    }

  }

}
