// Image details

.image-upload {
  flex-direction: column;
  width: 100%;
  max-width: 100%;

  .dropzone {
    position: relative;
    border: 1px solid transparent;
  }

  .dropzoneActive {
    border: 1px dashed $primary;
    padding: 0;
    margin: 0;

    &:after {
      position: absolute;
      top: 0;
      left: 0;
      content: '';
      width: 100%;
      height: 100%;
      background: rgba($primary, .1);
    }
  }

  .image-container {
    position: relative;
    // min-height: 200px; // TODO: make variable _imagefield.scss > .no-image-container.height

    .image-overlay {
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: rgba($white, 0.8);
      opacity: 0;
      transition: all 350ms;

      .delete {
        position: absolute;
        top: 15px;
        right: 15px;
        cursor: pointer;
      }

      .button {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
      }

      &:hover {
        opacity: 1;
      }

    }

    img {
      // height: 100% !important;
    }

  }
}
