@import '../../../../../scss/base/palette',
        '../../../../../scss/base/typography',
        '../../../../../scss/components/select/mixins',
        '../../../../../scss/helpers/variables';

.user-profile {
  display: block;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  color: $white;
  background: $black;
  border-top: 1px solid lighten($dark-grey, 3);
  transition: transform .2s linear;
  transform: translateY(calc(100% - 3rem));
  will-change: normal;

  @include ie-11
  {
      -ms-transform: translateY(84%);
      width: $sidebar-width;
  }

  &.expanded {
    background: #101010;
    transform: translateY(0);
  }

  // User info

  .user-block {
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: background .15s;
    height: 3rem;

    &:hover {
      background: #2b2b2b;
      cursor: pointer;
    }

    .userinfo {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
      margin-left: 2em;
      overflow-x: hidden;
      height: 100%;

      .name {
        font-size: $no-text;
        padding-bottom: 0;
        margin-top: -.5em;
        text-transform: inherit;
        font-weight: 500;
        line-height: 1.6em;
        text-overflow: ellipsis;
        overflow: hidden;
        width: 100%;
      }

      .role {
        font-size: $no-text;
        color: $medium-grey;
        line-height: 1em;
        text-transform: capitalize;
      }

      .account-info {
        margin-top: 2.1em;
      }

    }

    .expand-user-block--arrow {
      margin-left: 1em;
      margin-right: 2em;
      transition: transform .2s linear;

      &.rotated {
        transform: rotate(-180deg);
      }

    }

  }

  .userinfo-right {
    display: flex;
    align-items: center;
  }

  .user-profile-links {
    padding: 2em;
  }

  .product-updates--indicator {
    width: 6px;
    height: 6px;
    border-radius: 3px;
    background: $primary;
    margin-left: 2em;

    opacity: 1;
    transition: opacity .25s ease;

    &.expanded { opacity: 0; }

  }

  // Menu

  .nav-item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    line-height: 1.5em;
    padding-bottom: 1.2em;
    font-size: $sm-text;
    text-transform: uppercase;
    opacity: .7;
    cursor: pointer;

    &:hover {
      opacity: 1;
    }

    a {
      border: 0;
    }

    i {
      display: block;
      margin-top: -.1em;
    }

    &.upgrade-plan {
      display: flex;
      justify-content: space-between;
      align-items: center;

      .current-plan {
        font-size: $xxsm-text;
        padding: 0 1.4em;
        border-radius: $border-radius;
        color: $medium-grey;
        background-color: $dark-grey;
      }

    }

    &:hover {
      color: $primary;
      .product-updates--count { color: $white; }
    }

    &:last-child { padding-bottom: 0; }

    .dot { margin: 0 .5em; }

    > * {
      display: flex;
      width: 100%;
    }

  }

  .product-updates {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    position: relative;
  }

  .product-updates--count {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);

    background: $primary-medium;
    min-width: 24px;
    height: 24px;
    padding: 8px;
    border-radius: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;

    font-size: .75rem;
    font-weight: 500;
  }

}
