@import "../../../../scss/base/palette";
@import "../../../../scss/helpers/mixins";
@import "../../../../scss/helpers/variables";

.delete {
  position: absolute;
  display: block;
  width: 35px;
  text-align: center;
  line-height: 2em;
  height: 35px;
  top: 0;
  right: 0;
  opacity: .5;

  &:hover {
    opacity: 1;
    cursor: pointer;
  }

}

.button {
  align-self: center;
}

.disabled {
  opacity: 0.25;

  &:hover {
    opacity: 0.3;
    cursor: default;
  }

}

.helpBannerContainer
{
  margin-top: 3rem;
}

.dropzone {
  position: relative;
  border: 1px solid transparent;
  min-height: calc(100vh - 10rem);
  @media screen and (max-width: $breakpoint-small) {
    min-height: calc(100vh - 13rem);
  }
}

.dropzoneActive {
  border: 1px dashed $primary;
  padding: 0 .5em;
  margin: 1rem -.5em 0 -.5em;

  &:after {
    position: absolute;
    top: 0;
    left: 0;
    content: '';
    width: 100%;
    height: 100%;
    background: rgba($primary, .1);
  }

}

.mediaSearchContainer {
  padding: 0 2rem;
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  left: $sidebar-width;
  width: calc(100% - #{$sidebar-width});
  height: 5rem;
  z-index: 701;
  pointer-events: none;

  @media screen and (max-width: $breakpoint-small) {
    left: 0;
    width: 100%;
    top: 4rem;
  }

}

form.mediaSearch {
  z-index: 701;
  margin-top: 10px;
  margin-right: 3rem !important;
  width: 100%;
  height: 4rem;
}
