@import '../../../../scss/base/palette',
        '../../../../scss/base/typography',
        '../../../../scss/helpers/variables';


// Notifications icon
.notifications-container {
  line-height: 0;
}

// The circle indicator
.system-status {
  position: relative;
  margin-left: 1em;

  .indicator {
    width: 14px;
    height: 14px;
    border: 2px solid lighten($dark-grey, 10);
    border-radius: 100%;
    cursor: pointer;

    &.idle { border-color: $light-grey; opacity: .6; }

    &.thinking {
      border-top-color: $dark-grey;
      -webkit-animation: loaderSpin 1s infinite linear;
      animation: loaderSpin 1s infinite linear;
    }

    &.success { border-color: $primary; }
    &.error { border-color: $error; }
    &.warning { border-color: $warning; }
  }

}

// Notification count
.indicator--unread-count {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: -1.25em;
  right: -1.25em;
  height: 1.7em;
  min-width: 1.7em;
  border-radius: 1em;
  background: #282828;
  color: white;
  font-size: 0.54rem;
  font-weight: 700;
  text-align: center;
  padding: .55em;
}

.circle-status {
  display: inline-block;
  width: .5em;
  height: .5em;
  border-radius: .25em;

  &.circle-status--success { background: $primary; }
  &.circle-status--error { background: $error; }
  &.circle-status--warning { background: $warning; }

}

@keyframes loaderSpin {
  0%   { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}


// Activity modal

.activity-container {
  position: absolute;
  top: 5em;
  right: 1em;
  width: 320px;
  max-width: 90%;
  display: flex;
  flex-direction: column;
  padding: 1em;
  border: 1px solid lighten($medium-grey, 10);
  border-radius: $border-radius;
  background-color: $white;
  box-shadow: 0 20px 20px -10px rgba(40, 40, 40, 0.1);
  color: $dark-grey;
  z-index: 1000;

  @media screen and (min-width: $breakpoint-small) {
    top: .5em;
    left: calc(#{$sidebar-width} + 1em);
    width: 320px;
    max-width: 70vw;
  }

  // Arrow background
  &:before {
    position: absolute;
    content: '';
    width: 14px;
    height: 14px;
    top: -1px;
    right: 3.75em;
    background: lighten($medium-grey, 10);
    transform: translate(-50%,-50%) rotate(45deg);

    @media screen and (min-width: $breakpoint-small) {
      top: 2em;
      left: -1px;
    }

  }

  // Arrow fill
  &:after {
    position: absolute;
    content: '';
    width: 14px;
    height: 14px;
    top: 0px;
    right: 3.75em;
    background: $white;
    transform: translate(-50%,-50%) rotate(45deg);

    @media screen and (min-width: $breakpoint-small) {
      top: 2em;
      left: 0px;
    }

  }

  .activity-container--header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 1em;
  }

  .activity-title {
    font-size: $xsm-text;
    display: inline-block;
    padding: 0;
  }

  .activity-container--close {
    display: inline-block;
    cursor: pointer;
  }

  .activity-list {
    overflow: auto;
    -ms-overflow-style: none;
    max-height: 60vh;
  }

  .activity-list::-webkit-scrollbar {
   display: none;
  }

  .activity-item {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: .6em 0;
    border-bottom: 1px solid $light-grey;

    &.hero {
      padding: 1em 0 2em 0;
      justify-content: center;
      text-align: center;

      .activity-item--title { font-weight: $font-weight-semibold; }

      .indicator {
        align-self: center;
        margin-bottom: .8em;
        width: 32px;
        height: 32px;
        border: 1px solid $light-medium-grey;
        border-radius: 100%;
        cursor: pointer;

        &.idle { border-color: $light-grey; opacity: .6; }

        &.thinking {
          border-top-color: $dark-grey;
          -webkit-animation: loaderSpin 1s infinite linear;
          animation: loaderSpin 1s infinite linear;
        }

        &.success { border-color: $primary; }
        &.error { border-color: $error; }
        &.warning { border-color: $warning; }

      }

    }

    &:last-of-type { border: 0; }

  }

  .activity-item--left {
    flex: 0 0 90%;
    display: flex;
    flex-direction: column;
  }

  .activity-item--right { margin-left: auto; }

  .activity-item--title {
    line-height: 1.58;
    font-weight: $font-weight-normal;
  }

  .activity-item--time {
    font-size: $xxsm-text;
    text-transform: uppercase;
    color: $medium-grey;
    font-weight: $font-weight-semibold;
    padding-top: .5em;
  }

  .activity-item--error {
    width: 100%;
    background: #f9f9f9;
    border-radius: $border-radius;
    font-size: $xxsm-text;
    font-weight: $font-weight-bold;
    color: $error;
    padding: .5em;
    text-align: center;
    margin-top: 1rem;
    text-transform: uppercase;
    cursor: pointer;
    transition: background .2s ease;

    &:hover { background: darken(#f9f9f9, 5); }

  }

  .activity-item--message {
    width: 100%;
    background: #f9f9f9;
    border-radius: $border-radius;
    font-size: $xxsm-text;
    font-weight: $font-weight-bold;
    color: $dark-grey;
    padding: .5em;
    text-align: center;
    margin-top: 1rem;
    text-transform: uppercase;
    cursor: pointer;
    transition: background .2s ease;

    &:hover { background: darken(#f9f9f9, 5); }

  }

  .activity-item--error, .activity-item--message {
    & + .activity-item--error,
    & + .activity-item--message {
      margin-top: .5rem;
    }
  }

}
