.list-overlay--loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 4rem;
  left: 0;
  width: 100%;
  height: calc(100% - 4rem);
  background: rgba($light-grey, .8);
  z-index: 999;

  @media screen and (min-width: $breakpoint-small) {
    top: 0;
    height: 100%;
    left: $sidebar-width;
    width: calc(100% - #{$sidebar-width});
  }

  h2 { font-weight: $font-weight-normal; }

  .indicator {
    display: block;
    height: 46px;
    width: 46px;
    margin-bottom: 2em;
  }

}
