@import "~codemirror/lib/codemirror.css";


// Base editor styles
.editor .pane-wrapper {
  width: 100%;
  height: 100%;
  .raw { height: 100%; }
  .CodeMirror { height: 100%; }
  .CodeMirror-lines { padding-bottom: 50vh; } // Scroll past end
}


// CodeMirror codeblock
.ProseMirror .CodeMirror {
  width: 100%;
  height: auto;
  border: 1px solid $light-medium-grey;
  border-radius: $border-radius;
  margin-bottom: 1rem;

  .CodeMirror-sizer {
    min-height: auto;
  }

}


// CodeMirror theme
.cm-s-forestry.CodeMirror { font-family: $font-family-code; font-size: 14px; background: transparent; color: #3A3A3A; }
.cm-s-forestry div.CodeMirror-selected { background: #e0e0e0; }
.cm-s-forestry .CodeMirror-line::selection, .cm-s-forestry .CodeMirror-line > span::selection, .cm-s-forestry .CodeMirror-line > span > span::selection { background: $primary; }
.cm-s-forestry .CodeMirror-line::-moz-selection, .cm-s-forestry .CodeMirror-line > span::-moz-selection, .cm-s-forestry .CodeMirror-line > span > span::-moz-selection { background: $primary; }
.cm-s-forestry .CodeMirror-gutters { background: transparent; border-right: 0px; }
.cm-s-forestry .CodeMirror-guttermarker { color: $primary-dark; }
.cm-s-forestry .CodeMirror-guttermarker-subtle { color: #b0b0b0; }
.cm-s-forestry .CodeMirror-linenumber { color: $light-medium-grey; }
.cm-s-forestry .CodeMirror-cursor { border-left: 1px solid #505050; }

.cm-s-forestry span.cm-comment { color: $medium-grey; }
.cm-s-forestry span.cm-atom { color: $primary-dark; }
.cm-s-forestry span.cm-number { color: #6A7481; }

.cm-s-forestry span.cm-property, .cm-s-forestry span.cm-attribute { color: #6A7481; }
.cm-s-forestry span.cm-keyword { color: $primary-dark; }
.cm-s-forestry span.cm-string { color: $primary-dark; }

.cm-s-forestry span.cm-variable { color: $primary-dark; }
.cm-s-forestry span.cm-variable-2 { color: $primary-dark; }
.cm-s-forestry span.cm-def { color: $primary-dark; }
.cm-s-forestry span.cm-bracket { color: #6A7481; }
.cm-s-forestry span.cm-tag { color: #6A7481; }
.cm-s-forestry span.cm-link { color: $primary-dark; }
.cm-s-forestry span.cm-error { color: darken($warning, 10); }

.cm-s-forestry .CodeMirror-activeline-background { background: $lighter-grey; }
.cm-s-forestry .CodeMirror-matchingbracket { text-decoration: underline; }
