.field--single-file {

  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 0.3rem;
  border: 1px solid $light-grey;
  padding: 5px 10px;

  .delete {
    cursor: pointer;
    margin-right: 10px;
    padding: 5px;
  }

  .field--single-file--filename {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    user-select: none;
    font-size: .9rem;
  }

  .field--single-file--controls {
    display: flex;
    align-items: center;
  }

}
