@import "../../../../../../scss/base/palette";
@import "../../../../../../scss/helpers/mixins";
@import "../../../../../../scss/helpers/variables";

$gutter: 1rem;

.galleryGrid {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-start;
  margin: 0 (-$gutter);
}

.addNew {
  border: 1px dashed $medium-grey;
  position: relative;
  color: $medium-grey;

  // Plus sign
  &:after {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 36px;
    line-height: 1;
    position: absolute;
    content: '+';
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
  }

  &:hover {
    cursor: pointer;

    &:after {
      background: $light-grey;
    }

  }

}

.imageContainer {
  flex: 0 0 calc(50% - #{$gutter} * 2);
  margin: $gutter;
  background-size: cover;
  background-position: center;
  @include aspect-ratio(1,1);
  transition: all .08s ease;
  cursor: move;

  &:not(.addNew)
  {
    border: 1px solid $light-grey;
  }

  @media screen and (min-width: $breakpoint-smallest) {
    flex: 0 0 calc(33.3% - #{$gutter} * 2);
  }

  @media screen and (min-width: $breakpoint-large) {
    flex: 0 0 calc(25% - #{$gutter} * 2);
  }

  .imageOverlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($light-grey, .8);
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    transition: opacity 160ms ease;
    border: 1px solid transparent;

    &.uploading { opacity: 1; }

    &:hover { opacity: 1; }

    .delete {
      position: absolute;
      display: block;
      width: 35px;
      text-align: center;
      line-height: 2em;
      height: 35px;
      top: 0;
      right: 0;

      &:hover {
        cursor: pointer;
      }

    }

  }

}

.dropzone {
  position: relative;
  border: 1px solid transparent;
}

.dropzoneActive {
  border: 1px dashed $primary;
  padding: 0 1em;
  margin: 0 -1em;

  &:after {
    position: absolute;
    top: 0;
    left: 0;
    content: '';
    width: 100%;
    height: 100%;
    background: rgba($primary, .1);
  }

}
