@import "../../../../scss/base/palette",
        "../../../../scss/helpers/variables",
        "../../../../scss/base/typography";

.toggle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 3rem;
  height: 3rem;
  margin-right: -1.25rem;
  cursor: pointer;
}

.dotdotdot {
  position: relative;
  width: 4px;
  height: 4px;
  border-radius: 2px;
  background: $medium-grey;

  &:before {
    position: absolute;
    content: '';
    left: -8px;
    width: 4px;
    height: 4px;
    border-radius: 2px;
    background: $medium-grey;
  }

  &:after {
    position: absolute;
    content: '';
    right: -8px;
    width: 4px;
    height: 4px;
    border-radius: 2px;
    background: $medium-grey;
  }
}

.narrowOnly {
  @media screen and (min-width: 1150px) {
    display: none;
  }
}

.actions {
  display: block;
  position: absolute;
  background: white;
  top: 3rem;
  right: -0.5rem;
  border: 1px solid lighten($medium-grey, 12);
  border-radius: 0.35rem;
  min-width: 120px;
  z-index: 100;
  will-change: normal;
  opacity: 0;
  transform: scale(.5);
  transition: opacity .25s ease, transform .25s ease;
  pointer-events: none;

  &.active {
    opacity: 1;
    transform: scale(1);
    pointer-events: all;
  }

  li {
    font-size: $no-text;
    border-bottom: 1px solid lighten($medium-grey, 12);
    padding: 0.5rem;

    &:last-of-type {
      border: none;
    }
    
  }
}
